*{
  box-sizing: border-box;
}

body{
  font-family: "Karla", sans-serif;
  margin: 0;
}

main{
  padding: 36px;
}

.header{
  display: flex;
  align-items: center;
  height: 65px;
  background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
  color: white;
  padding: 20px;
}

.pic1{
  height: 100%;
  margin-right: 6px;
}

.meme{
  font-size: 20px;
  margin-right: auto;
}

.project{
  font-size: 12px;
  font-weight: 500;
}

.form{
  display: grid;
  grid-template: 40px 40px / 1fr 1fr;
  gap: 17px;
}

.form-input{
  font-family: "Karla", sans-serif;
  border-radius: 5px;
  border: 1px solid #D5D4D8;
  text-indent: 5px;
}

.form-btn{
  grid-column: 1 / -1;
  font-family: "Karla", sans-serif;
  border-radius: 5px;
  background: linear-gradient(90.41deg, #711F8D 1.14%, #A818DA 100%);
  color: white;
  border: none;
  cursor: pointer;
}

.group{
  padding-left: 39px;
  width: 1000px;
}